import Drama from '../models/Drama';
import Person from '../models/Person';
import Author from '../models/Author';
import Theme from '../models/Theme';
import OuterChain from '../models/OuterChain';

const generatePinkx = (drama: Drama, theme: Theme): string => {
  return `
<pinkx id="pinkx">
${generateTitle(drama)}
${generateCover(drama)}
${generateContent(drama, theme)}
${generateLinks(drama.links)}
${generateState()}
</pinkx>
<link rel="stylesheet" type="text/css" media="screen" href="${theme.css}" />
  `;
};

const generateTitle = (drama: Drama): string => {
  return `
<section id="title" data-title="${drama.title}">
<h1>${drama.title}</h1>
<h5>${drama.episode}</h5>
<p>${drama.subtitle}</p>
</section>
  `;
};

const generateCover = (drama: Drama): string => {
  return `
<section id="cover">
<imgChunk class="img" style="background-image:url(${drama.dramaPoster})"></imgChunk>
</section>
  `;
};

const generateContent = (drama: Drama, theme: Theme): string => {
  return `
<section id="content">
${generatePeople(drama.staff, 'staff', '制作组', theme.author)}
${generatePeople(drama.cast, 'cast', '配音组')}
${generatePeople(drama.ed, 'ed', '片尾曲')}
</section>
  `;
};

const generatePeople = (people: Person[], className: string, name: string, author?: Author): string => {
  const pinkx: string =
    className === 'staff' && !!author
      ? `
<item><a href="https://weibo.com/n/${author.weibo}" class="member" target="_black">
<span class="job">宣传</span><span class="name">${author.name}&pinkX</span></a></item>
      `
      : '';

  return `
<group class="${className}">
<h3>${name}</h3>
    ${people
      .map(person => {
        return `
<item>
<a ${person.weibo ? `href="https://weibo.com/n/${person.weibo}" target="_black"` : ' '} class="item">
<span class="job">${person.job}</span>
<span class="name">${person.name}</span>
${person.organization ? `<span class="org">${person.organization}</span>` : ''}
</a>
</item>
        `;
      })
      .join('')}
${pinkx}
</group>
  `;
};

const generateLinks = (links: OuterChain[]): string => {
  return `
<section id="links">
<h3>链接</h3>
      ${links
        .map(link => {
          return `
<item>
<a href=${link.url} class="link" target="_black">${link.name}</a>
${link.other ? `<span class="other">${link.other}</span>` : ''}
</item>
          `;
        })
        .join('')}
</section>
  `;
};

const generateState = (): string => {
  return `
<section id="state">
<h3>- 声明 -</h3>
<item>本作品仅供配音爱好者学习、交流使用，请勿进行二次修改和用于商业用途。</item>
<item>转载时请保留以上信息完整，谢谢合作！</item>
</section>
  `;
};

export default generatePinkx;
